import { KeyValue } from '../../lib/keyboard'
import { getStatuses } from '../../lib/statuses'
import { Key } from './Key'
import { useEffect } from 'react'
import { ENTER_TEXT, DELETE_TEXT, SHIFT_TEXT } from '../../constants/strings'

type Props = {
  onChar: (value: string) => void
  onDelete: () => void
  onEnter: () => void
  onShift: () => void
  guesses: string[]
  isShifted: boolean
}

export const Keyboard = ({ onChar, onDelete, onEnter, onShift, guesses, isShifted }: Props) => {
  const charStatuses = getStatuses(guesses)

  const onClick = (value: KeyValue) => {
    if (value === 'ENTER') {
      onEnter()
    } else if (value === 'SHIFT') {
      onShift()
    } else if (value === 'DELETE') {
      onDelete()
    } else {
      onChar(value)
      if (isShifted) onShift()
    }
  }

  useEffect(() => {
    const listener = (e: KeyboardEvent) => {
      if (e.code === 'Enter') {
        onEnter()
      } else if (e.code === 'Backspace') {
        onDelete()
      } else {
        const key = e.key.toUpperCase()
        if (key.length === 1 && key >= 'A' && key <= 'Z') {
          onChar(key)
        }
      }
    }
    window.addEventListener('keyup', listener)
    return () => {
      window.removeEventListener('keyup', listener)
    }
  }, [onEnter, onDelete, onChar])

  if(isShifted){
  return (
    <div>
      <div className="flex justify-center mb-1">
        <Key value="" onClick={onClick} status={charStatuses['']} />
        <Key value="" onClick={onClick} status={charStatuses['']} />
        <Key value="" onClick={onClick} status={charStatuses['']} />
        <Key value="" onClick={onClick} status={charStatuses['']} />
        <Key value="" onClick={onClick} status={charStatuses['']} />
        <Key value="" onClick={onClick} status={charStatuses['']} />
        <Key value="" onClick={onClick} status={charStatuses['']} />
        <Key value="" onClick={onClick} status={charStatuses['']} />
        <Key value="" onClick={onClick} status={charStatuses['']} />
        <Key value="" onClick={onClick} status={charStatuses['']} />
      </div>
      <div className="flex justify-center mb-1">
        <Key value="" onClick={onClick} status={charStatuses['']} />
        <Key value="" onClick={onClick} status={charStatuses['']} />
        <Key value="" onClick={onClick} status={charStatuses['']} />
        <Key value="" onClick={onClick} status={charStatuses['']} />
        <Key value="" onClick={onClick} status={charStatuses['']} />
        <Key value="" onClick={onClick} status={charStatuses['']} />
        <Key value="" onClick={onClick} status={charStatuses['']} />
        <Key value="" onClick={onClick} status={charStatuses['']} />
        <Key value="" onClick={onClick} status={charStatuses['']} />
        <Key width={65.4} value="DELETE" onClick={onClick}>
          {DELETE_TEXT}
        </Key>
      </div>
      <div className="flex justify-center">
        <Key width={65.4} value="SHIFT" onClick={onClick} status="absent">
            {SHIFT_TEXT}
        </Key>
        <Key value="" onClick={onClick} status={charStatuses['']} />
        <Key value="" onClick={onClick} status={charStatuses['']} />
        <Key value="" onClick={onClick} status={charStatuses['']} />
        <Key value="" onClick={onClick} status={charStatuses['']} />
        <Key value="" onClick={onClick} status={charStatuses['']} />
        <Key value="" onClick={onClick} status={charStatuses['']} />
        <Key value="" onClick={onClick} status={charStatuses['']} />
        <Key width={65.4} value="ENTER" onClick={onClick}>
          {ENTER_TEXT}
        </Key>
      </div>
    </div>
  )
  } else {
    return (
      <div>
        <div className="flex justify-center mb-1">
          <Key value="" onClick={onClick} status={charStatuses['']} />
          <Key value="" onClick={onClick} status={charStatuses['']} />
          <Key value="" onClick={onClick} status={charStatuses['']} />
          <Key value="" onClick={onClick} status={charStatuses['']} />
          <Key value="" onClick={onClick} status={charStatuses['']} />
          <Key value="" onClick={onClick} status={charStatuses['']} />
          <Key value="" onClick={onClick} status={charStatuses['']} />
          <Key value="" onClick={onClick} status={charStatuses['']} />
          <Key value="" onClick={onClick} status={charStatuses['']} />
          <Key value="" onClick={onClick} status={charStatuses['']} />
        </div>
        <div className="flex justify-center mb-1">
          <Key value="" onClick={onClick} status={charStatuses['']} />
          <Key value="" onClick={onClick} status={charStatuses['']} />
          <Key value="" onClick={onClick} status={charStatuses['']} />
          <Key value="" onClick={onClick} status={charStatuses['']} />
          <Key value="" onClick={onClick} status={charStatuses['']} />
          <Key value="" onClick={onClick} status={charStatuses['']} />
          <Key value="" onClick={onClick} status={charStatuses['']} />
          <Key value="" onClick={onClick} status={charStatuses['']} />
          <Key value="" onClick={onClick} status={charStatuses['']} />
          <Key width={65.4} value="DELETE" onClick={onClick}>
            {DELETE_TEXT}
          </Key>
        </div>
        <div className="flex justify-center">
          <Key width={65.4} value="SHIFT" onClick={onClick}>
              {SHIFT_TEXT}
          </Key>
          <Key value="" onClick={onClick} status={charStatuses['']} />
          <Key value="" onClick={onClick} status={charStatuses['']} />
          <Key value="" onClick={onClick} status={charStatuses['']} />
          <Key value="" onClick={onClick} status={charStatuses['']} />
          <Key value="" onClick={onClick} status={charStatuses['']} />
          <Key value="" onClick={onClick} status={charStatuses['']} />
          <Key value="" onClick={onClick} status={charStatuses['']} />
          <Key width={65.4} value="ENTER" onClick={onClick}>
            {ENTER_TEXT}
          </Key>
        </div>
      </div>
    )
  }
}
